import { Action, State, STATE_VERSION, SurveyConfig } from './types';

export const initialState: State = {
  version: STATE_VERSION,
  hash: '',
  step: 0,
  responses: {},
  validSteps: [],
  profile: {
    name: '',
    friends: 0,
    followers: 0,
    loaded: false,
  },
  post: {
    id: '',
    img: { src: '' },
    likes: 0,
    shares: 0,
    views: 0,
  },
  messages: [],
  friend1: '',
  friend2: '',
  variables: {
    lostFriends: 0,
  },
};

export const initWithConfig =
  (config: SurveyConfig) =>
  (initState: State): State => ({
    ...initState,
    validSteps: Array(Object.keys(config).length).fill(false),
  });

// export const init = initWithConfig(config);

export const reducerWithConfig =
  (config: SurveyConfig) =>
  (state: State, action: Action): State => {
    // console.log('state', state);
    // console.log('action', action);

    const newState = reduce(config)(state, action);
    // console.log('newState', newState);

    return newState;
  };

const getStepById = (config: SurveyConfig, id: string, fallback = -1) => {
  const step = Object.keys(config).findIndex((key) => key === id);
  return step !== -1 ? step : fallback;
};

const reduce =
  (config: SurveyConfig) =>
  (state: State, action: Action): State => {
    const { type } = action;
    // console.log(state);

    switch (type) {
      case 'RESPONSE':
        return {
          ...state,
          responses: {
            ...state.responses,
            [action.response.id]: action.response,
          },
        };

      case 'STEP_SET_VALID':
        return {
          ...state,
          validSteps: state.validSteps.map((valid, step) =>
            step === action.step ? action.valid : valid,
          ),
        };

      case 'NEXT':
        console.log({
          type,
          currentStep: state.step,
          isValid: state.validSteps[state.step],
          configLength: Object.keys(config).length,
          maybeNextStep: state.step + 1,
          actualNextStep: !state.validSteps[state.step] ? state.step : state.step + 1,
        });

        if (!state.validSteps[state.step]) return { ...state };
        return {
          ...state,
          step: state.step + 1,
        };

      case 'BACK':
        return {
          ...state,
          step: Math.max(state.step - 1, 0),
        };

      case 'RESET':
        return initWithConfig(config)(initialState);

      case 'GOTO_STEP':
        console.log({
          type,
          currentStep: state.step,
          nextStep: action.step,
          isValid: state.validSteps[state.step],
          responesLength: Object.keys(state.responses).length,
        });

        return {
          ...state,
          step: action.step,
        };

      case 'GOTO_STEP_ID':
        console.log({
          type,
          currentStep: state.step,
          nextStepId: action.id,
          nextStep: getStepById(config, action.id),
          isValid: state.validSteps[state.step],
          responesLength: Object.keys(state.responses).length,
        });

        return {
          ...state,
          step: getStepById(config, action.id),
        };

      case 'HANDLE_RESPONSE':
        return {
          ...state,
          // TODO: May not be needed
        };

      case 'RESET_RESPONSE':
        return {
          ...state,
          responses: {
            ...Object.entries(state.responses)
              .filter(([id]) => id !== action.id)
              .reduce((ret, [id, response]) => ({ ...ret, [id]: { ...response } }), {}),
          },
        };

      case 'CUSTOM_HANDLER':
        return action.handler(state);

      case 'SET_MESSAGES':
        return {
          ...state,
          messages: [...action.messages],
        };

      default:
        throw new Error(`Unknown action type: ${type}`);
    }
  };
